import React from "react";
import { NavLink, Link } from "react-router-dom";
import logo from "../../assets/logo-oficial.png";

const NavAlmacen = () => {
  return (
    <>
      <nav className="navbar navbar-expand-lg bg-body-tertiary">
        <div className="container-fluid">
          <Link className="navbar-brand" to={"/almacen"}>
            <img
              src={logo}
              alt="Logo"
              width="70"
              height="60"
              className="d-inline-block align-text-top"
            />
          </Link>
          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav me-auto mb-2 mb-lg-0">
              {/* REQUISICIONES */}
              <li className="nav-item dropdown">
                <Link
                  className="nav-link dropdown-toggle"
                  role="button"
                  data-bs-toggle="dropdown"
                  aria-expanded="false"
                >
                  Requisiciones
                </Link>
                <ul className="dropdown-menu">
                  <li>
                    <Link
                      className="dropdown-item"
                      to={"/almacen/requisicion-molienda"}
                    >
                      Molienda
                    </Link>
                  </li>
                  <li>
                    <Link
                      className="dropdown-item"
                      to={"/almacen/requisicion-seleccion"}
                    >
                      Seleccion
                    </Link>
                  </li>
                  {/* <li>
                    <hr className="dropdown-divider" />
                  </li>
                  <li>
                    <a className="dropdown-item" href="#">
                      Something else here
                    </a>
                  </li> */}
                </ul>
              </li>

              {/* MATERIAS PRIMAS */}
              <li className="nav-item dropdown">
                <Link
                  className="nav-link dropdown-toggle"
                  role="button"
                  data-bs-toggle="dropdown"
                  aria-expanded="false"
                >
                  Materia Prima
                </Link>
                <ul className="dropdown-menu">
                  <li>
                    <Link
                      className="dropdown-item"
                      to={"/almacen/materia-prima"}
                    >
                      Administrar
                    </Link>
                  </li>
                  <li>
                    <Link
                      className="dropdown-item"
                      to={"/almacen/materia-prima/crear"}
                    >
                      Crear
                    </Link>
                  </li>
                  {/* <li>
                    <hr className="dropdown-divider" />
                  </li>
                  <li>
                    <a className="dropdown-item" href="#">
                      Something else here
                    </a>
                  </li> */}
                </ul>
              </li>

              {/* PROVEEDORES */}
              <li className="nav-item dropdown">
                <Link
                  className="nav-link dropdown-toggle"
                  role="button"
                  data-bs-toggle="dropdown"
                  aria-expanded="false"
                >
                  Proveedor
                </Link>
                <ul className="dropdown-menu">
                  <li>
                    <Link className="dropdown-item" to={"/almacen/proveedor"}>
                      Administrar
                    </Link>
                  </li>
                  <li>
                    <Link
                      className="dropdown-item"
                      to={"/almacen/proveedor/crear"}
                    >
                      Crear
                    </Link>
                  </li>
                  {/* <li>
                    <hr className="dropdown-divider" />
                  </li>
                  <li>
                    <a className="dropdown-item" href="#">
                      Something else here
                    </a>
                  </li> */}
                </ul>
              </li>

              <li className="nav-item">
                <NavLink
                  className="nav-link"
                  aria-current="page"
                  to={"/almacen/entradas-stock/crear"}
                >
                  Entradas Stock
                </NavLink>
              </li>
            </ul>
            <ul className="navbar-nav d-none d-lg-flex ml-2 order-3">
              <li className="nav-item">
                <a
                  className="nav-link"
                  href="https://themes.getbootstrap.com/signin/"
                >
                  Sign in
                </a>
              </li>
              <li className="nav-item">
                <a
                  className="nav-link"
                  href="https://themes.getbootstrap.com/my-account/"
                >
                  Sign up
                </a>
              </li>
            </ul>
          </div>
        </div>
      </nav>
    </>
  );
};

export default NavAlmacen;
